.bloom-research-bar{position:fixed;top:0px;width:100%;max-height:48px;padding:10px 20px;background-color:#fafafa;box-shadow:0 0.1px 0.3px rgba(0,0,0,0.06),0 1px 2px rgba(0,0,0,0.2);z-index:99999}.bloom-research-bar a{margin-right:40px}.bloom-research-bar *{margin:0}.research-bar .bloom-research-bar{position:absolute;top:46px}body.research-bar{margin-top:48px}@media screen and (min-width: 783px){.research-bar .bloom-research-bar{position:fixed;top:32px}}
